import React from "react";
import OurFlagCourses from "../HomeComponent/Courses";

export default function MemorizationSec() {
    return(
        <>
        <section className="learn-sec">
            <div className="learn-first-sec">
                <div className="content">
                    <h1>Importance and benefits of memorizing the Quran   (It comes under the Memorization the Quran)</h1>
                    <p>Allaah has given special privileges to the one who memorizes the Quran in a number of ways in this world and in the Hereafter, for example:</p>
                    <p>1  He takes precedence over others in leading the prayer.</p>
                    <p>It was narrated that Abu Masood al-Ansaari said: The Messenger of Allaah (peace and blessings of Allaah be upon him) said: The people should be led in prayer by the one among them who has the most knowledge of the Book of Allaah; if they are equal in knowledge of the Quraan, then by the one who has most knowledge of the Sunnah; if they are equal in knowledge of the Sunnah, then by the one who migrated (made hijrah) first; if they are equal in terms of hijrah, then by the one who became Muslim first. No man should lead another in prayer in his domain of authority, or sit in his place in his house, except with his permission.</p>
                    <p>(Narrated by Muslim, 673) </p>
                    <p>It was narrated that Abd-Allaah ibn Umar said: When the first muhaajiroon (emigrants) came to Quba, before the Messenger of Allaah (peace and blessings of Allaah be upon him) came (to Madeenah), Saalim the freed slave of Abu Hudhayfah used to lead them in prayer, and he was the one who knew the most Quraan.</p>
                    <p>(Narrated by al-Bukhaari, 660)</p>
                    <p>2  He is placed in front of others in a common grave, closer to the qiblah, if it is essential to bury him with others.</p>
                    <p>It was narrated that Jaabir ibn Abd-Allaah (may Allaah be pleased with them both) said: The Prophet would wrap two of the men slain at Uhud in a single cloth, then he would ask, Which of them knew more Quraan? If one of them was pointed out to him, he would put that one in the lahd (niche in the side of the grave) first. And he said, I will be a witness over these people on the Day of Resurrection. He commanded that they should be buried with their blood, without being washed, and that no funeral prayer should be offered for them.</p>
                    <p>(Narrated by al-Bukhaari, 1278)</p>
                    <p> 3  He takes precedence in leadership if he able to bear that</p>
                    <p> It was narrated from Aamir ibn Waathilah that Naafi ibn Abd al-Haarith met Umar in Usfaan, and Umar had appointed him as governor of Makkah. Umar said, Who have you appointed in charge of the people of the valley? He said, Ibn Abza. Umar asked, Who is Ibn Abza? He said, One of our freed slaves. Umar said, Have you appointed over them a freed slave?! He said, He is well-versed in the Book of Allaah and he has knowledge of the laws of inheritance. Umar said: Your Prophet (peace and blessings of Allaah be upon him) said: Indeed, by this Book, Allaah would exalt some people and degrade others.</p>
                    <p>(Narrated by Muslim, 817)</p>
                    <div className="center">
                        <img src="/img/memrize-page.webp" alt="" />
                    </div>
                    <p>With regard to the Hereafter:</p>
                    <p> 4  The status of the one who memorizes Quraan will be commensurate with the last aayah he memorized. </p>
                    <p>It was narrated from Abd-Allaah ibn Amr that the Prophet (peace and blessings of Allaah be upon him) said: It will be said to the companion of the Quraan (i.e., the one who memorized and studied it): Read, advance in status and recite as you used to do in the world, for your status will be commensurate with the last aayah that you recite.</p>
                    <p>(Narrated by al-Tirmidhi, 2914; he said this is a saheeh hasan hadeeth. Al-Albaani said in Saheeh al-Tirmidhi, no. 2329, it is hasan saheeh. Also narrated by Abu Dawood, 1464).</p>
                    <p> What is meant by reciting here is memorizing.</p>
                    <p>5  He will be with the angels, accompanying them. </p>
                    <p>It was narrated from Aaishah that the Prophet (peace and blessings of Allaah be upon him) said: The one who recites the Quraan and learns it by heart, will be with the noble righteous scribes (in Heaven) and the one who exerts himself to learn the Quraan by heart and recites it with great difficulty, will have a double reward.</p>
                    <p> (Narrated by al-Bukhaari, 4653; Muslim, 798) </p>
                    <p>6  He will be given a crown of honour and a garment of honour to wear. </p>
                    <p>It was narrated from Abu Hurayrah that the Prophet (peace and blessings of Allaah be upon him) said: The Quraan will come on the Day of Resurrection and will say, O Lord, adorn him. So he will be given a crown of honour to wear. Then it will say, O Lord, give him more. So he will be given a garment of honour. Then it will say, O Lord, be pleased with him. So Allaah will be pleased with him. Then it will be said to him, Recite and advance in status, and for each verse you will gain one more hasanah (reward for good deed).</p>
                    <p>(Narrated by al-Tirmidhi, 2915. He said, this is a saheeh hasan hadeeth. Al-Albaani said in Saheeh al-Tirmidhi, 2328, this is hasan).</p>
                    <p> 7  The Quraan will intercede for him with his Lord. </p>
                    <p>It was narrated that Abu Umaamah al-Baahili said: I heard the Messenger of Allaah (peace and blessings of Allaah be upon him) say, Recite the Quraan, for it will come on the Day of Resurrection to intercede for its companions. Recite the two bright ones, al-Baqarah and Soorat Aal Imraan, for they will come on the Day of Resurrection like two clouds or two shades or two flocks of birds in ranks, pleading for those who recite them. Recite Soorat al-Baqarah for to take recourse to it is a blessing and to give it up is a cause of grief, and the magicians cannot confront it.</p>
                    <p>(Narrated by Muslim, 804, and by al-Bukhaari in a muallaq report).</p>
                    <h1>Shahidi Qaida: </h1>
                    <p>Arabic wave has launched its own Tajweedi Qaida, Shahidi Qaida which we have made by extracting very minute details of Qaida from the Qurra.</p>
                    <h1>Separate Tutors for Separate Courses:</h1>
                    <p>For every course offered by Arabic Wave, we provide a separate tutor for separate courses who are experts in the field. These expert tutors are certified trainers. </p>
                    <h1>Female Tutor for Females:</h1>
                    <p>Arabic Wave offers you to select a female tutor for the female candidate.</p>
                </div>
            </div>
            <OurFlagCourses/>
        </section>
        </>
    )
}